// *
// * В этом файле перечислен начальный сброс глобальных стилей по селекторам тегов
// *

body {
  font-family: var(--main-font);
  letter-spacing: var(--main-letter-spacing);
}

h1,
h2,
h3,
h4 {
  margin: 0;
}

ul,
p {
  margin: 0;
  padding: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.link {
  text-decoration: none;
  color: inherit;
}

.list {
  list-style: none;
}

svg {
  fill: currentColor;
}

section {
  outline: 1px solid black;
}
