.modal {
    &__franchise {
        background-color: var(--background-accent);
        position: relative;
        text-align: center;
        padding: 20px 30px;
        max-width: 1090px;
        border-radius: 4px;
        box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
        @media screen and (max-width: $mobile-max) {
            top: 0;
            left: 0;
            margin: 0 auto;
            overflow-y: scroll;
        }
        @media screen and (min-width: $tablet-min) {
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 80%;
            padding: 25px 45px;
        }
        @media screen and (min-width: $desktop-min) {
            padding: 35px 55px;
        }
    }
}

.franchise {
    &__item {
        background-color: var(--main-white-color);
        border-radius: 4px;
        font-weight: 500;
        font-size: 12px;
        line-height: 1.86;
        padding: 42px 20px;
        max-width: 220px;
        background: var(--main-white-color);
        box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
        border-radius: 24px;
    }
    @media screen and (max-width: $mobile-max) {
        &__item {
            margin: 0 auto;
        }
        &__item:not(:last-child) {
            margin-bottom: 20px;
        }
    }
    @media screen and (min-width: $tablet-min) {
        &__list {
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            margin-top: -20px;
            margin-left: -20px;
        }
        &__item {
            max-width: 330px;
            margin-top: 20px;
            margin-left: 20px;
            flex-basis: calc(100% / 3 - 20px);
        }
    }
    @media screen and (min-width: $desktop-min) {
        &__list {
            margin-top: -30px;
            margin-left: -30px;
        }
        &__item {
            margin-top: 30px;
            margin-left: 30px;
            flex-basis: calc(100% / 3 - 30px);
        }
    }
    &__subscribe {
        font-weight: 500;
        font-size: 12px;
        line-height: 1.86;
        margin-top: 20px;
        margin-bottom: 20px;
        @media screen and (min-width: $tablet-min) {
            font-size: 14px;
        }
        @media screen and (min-width: $desktop-min) {
            font-size: 16px;
        }
    }
    &__link>.modal__btn {
        margin: 0 auto;
    }
}